<template>
  <header class="app-header">
    <button class="toggle-button" @click="$emit('toggle-sidebar')">
      ☰
    </button>
    <div class="search-container">
      <input v-model="searchQuery" placeholder="Search games..." class="search-input" />
      <button @click="searchGames" class="search-button">Search</button>
    </div>
    <button class="home-button" @click="goHome">Home</button>
  </header>
</template>

<script>
export default {
  name: "AppHeader",
  data() {
    return {
      searchQuery: ''
    };
  },
  methods: {
    searchGames() {
      if (this.searchQuery.trim()) {
        this.$router.push({ path: '/', query: { search: this.searchQuery } });
      }
    },
    goHome() {
      this.$router.push('/');
    }
  }
};
</script>

